#Timeline .TimelineItem:after,
#Timeline .TimelineItem:before {
  content: "";
  display: block;
  width: 100%;
  clear: both;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.Container {
  max-width: 1100px;
  margin: 30px auto;
  margin-bottom: 120px;
}

#Timeline {
  width: 100%;
  margin: 30px auto;
  position: relative;
  padding: 0 10px;
  transition: all 0.4s ease;
}

#Timeline:before {
  content: "";
  width: 3px;
  height: 100%;
  background: var(--terniary);
  left: 50%;
  top: 0;
  position: absolute;
}

#Timeline:after {
  content: "";
  clear: both;
  display: table;
  width: 100%;
}

#Timeline .TimelineItem {
  position: relative;
}

#Timeline .NotLast {
  margin-bottom: 40px;
}

#Timeline .TimelineItem .TimelineIcon {
  background: var(--terniary);
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 50%;
  overflow: hidden;
  margin-left: -23px;
  border-radius: 50%;
}

#Timeline .TimelineItem .TimelineIcon img {
  position: relative;
  top: 14px;
  left: 14px;
}

.TimelineIcon img {
  max-width: 23px;
  max-height: 23px;
}

#Timeline .TimelineItem .TimelineContent {
  width: 45%;
  background: var(--terniary);
  padding: 20px;
  -ms-box-shadow: 0 3px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  transition: all 0.3s ease;
}

#Timeline .TimelineItem .TimelineContent h2 {
  padding: 20px;
  letter-spacing: 0.05em;
  background: var(--quinary);
  color: var(--white);
  margin: -20px -20px 0 -20px;
  font-weight: 300;
  border-radius: 3px 3px 0 0;
}

#Timeline .TimelineItem .TimelineContent:before {
  content: "";
  position: absolute;
  left: 45%;
  top: 20px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 7px solid var(--quinary);
}

#Timeline .TimelineItem .TimelineContent.Right {
  float: right;
}

#Timeline .TimelineItem .TimelineContent.Right:before {
  content: "";
  right: 45%;
  left: inherit;
  border-left: 0;
  border-right: 7px solid var(--quinary);
}

